      align-items: center;
  height: 100vh;
  padding-top: 60px;
  z-index: 1;
  background-color: #fff;
  outline: 2px dashed salmon;
  height: calc(100vh - 100px);
  margin-bottom: 100px; }
  .contacts .title {
